export default {
	data() {
		return {
			listData: [], // 首页列表渲染的数据
			currentPage: 1, //当前请求页
			totalPage: 0, // 帖子总页数
			isShowDiv: false, //显示‘我是有界限的’
			canGoTop: false, //回顶部
			scrollTop: 0, // 滚动窗口位置
			old: {
				scrollTop: 0
			},
			isNoData: false //延迟2s拿数据
		};
	},
	onLoad() {
		this.init();
	},
	onPullDownRefresh() {
		this.init();
		setTimeout(() => {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		/**
		 * 初始化拿数据,清空原数据之后请求
		 */
		init() {
			this.currentPage = 1;
			this.listData = [];
			this.getListData();
			// 延迟获取数据
			this.isNoData = false;
			setTimeout(() => {
				this.isNoData = true;
			}, 5000);
		},

		/**
		 * 获取首页列表的数据
		 */
		getListData() {
			this.$http
				.get('/homepage/getSticker', {
					page: this.currentPage
				})
				.then(res => {
					this.listData.push(...res.data.stickers);
					this.totalPage = res.data.totalPage;
				});
		},

		/**
		 * 页面触底，加载更多数据
		 */
		lower() {
			this.currentPage++;
			if (this.currentPage > this.totalPage) {
				this.isShowDiv = true;
				return;
			}
			this.getListData();
		},

		/**
		 * 获取性别图片地址
		 */
		getSexImage(sex) {
			return require('@/static/image/' + (sex === 0 ? 'male.png' : 'female.png'));
		},

		/**
		 * 监听滚动位置，设置回顶部按钮
		 */
		scroll(e) {
			this.old.scrollTop = e.detail.scrollTop;
			this.canGoTop = e.detail.scrollTop > 2500;
		},

		/**
		 * 回顶部事件
		 */
		goTop() {
			this.scrollTop = this.old.scrollTop;
			this.$nextTick(function() {
				this.scrollTop = 0;
			});
			this.canGoTop = false;
		},

		/**
		 * 跳转首页详情
		 */
		navToHomeDetail(id) {
			uni.navigateTo({
				url: '/pages/home/homeDetail?id=' + id
			});
		}
	}
};
